/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	margin: 0;
	padding: 0;
	border: 0;
	outline: none;
	box-sizing: border-box;
}

body {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #06283D;
}

.container {
	position: relative;
	width: 400px;
	height: 105px;
	background: #fff;
	padding: 28px 32px;
	overflow: hidden;
	border-radius: 18px;
	font-family: 'Roboto', sans-serif;
	transition: 0.6s ease-out;
}

.search-box {
	width: 100%;
	height: min-content;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.search-box input {
	color: #06283D;
	width: 80%;
	font-size: 20px;
	font-weight: 500;
	text-transform: uppercase;
	padding-left: 32px;
}

.search-box input::placeholder {
	font-size: 20px;
	font-weight: 500;
	color: #06283D;
	text-transform: capitalize;
}

.search-box button {
	cursor: pointer;
	width: 50px;
	height: 50px;
	color: #06283D;
	background: #dff6ff;
	font-size: 22px;
	transition: 0.4s ease;
}

.search-box button:hover {
	color: #fff;
	background: #06283D;
}

.search-box i{
	position: absolute;
	color: #06283D;
	font-size: 28px;
}

.weather-box {
	text-align: center;
}

.weather-box img {
	width: 20%;
	margin: 8px;
}

.weather-box .temperature {
	position: relative;
	color: #06283D;
	font-size: 3rem;
	font-weight: 880;
	margin-left: -16px;
}

.weather-box .temperature span {
	position: absolute;
	margin-left: 4px;
	font-size: 1.5rem;
}

.weather-box .description {
	color: #06283D;
	font-size: 22px;
	font-weight: 500;
	text-transform: capitalize;
}

.weather-details {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.weather-details .humidity,
.weather-details .wind {
	display: flex;
	align-items: center;
	width: 50%;
	height: 100px;
}

.weather-details .humidity {
	padding-left: 20px;
	justify-content: flex-start;
}

.weather-details .wind {
	padding-right: 20px;
	justify-content: flex-end;
}

.weather-details i {
	color: #06283D;
	font-size: 26px;
	margin-right: 10px;
	margin-top: 6px;	
}

.weather-details span {
	color: #06283D;
	font-size: 22px;
	font-weight: 500;
}

.weather-details p {
	color: #06283D;
	font-size: 14px;
	font-weight: 500;
}

.not-found {
	width: 100%;
	text-align: center;
	margin-top: 50px;
	scale: 0;
	opacity: 0;
	display: none;
}

.not-found img {
	width: 70%;
}

.not-found p {
	color: #06283D;
	font-size: 22px;
	font-weight: 500;
	margin-top: 12px;
}

.weather-box, 
.weather-details {
	scale: 0;
	opacity: 0;
}

.fadeIn {
	animation: 0.5s fadeIn forwards;
	animation-delay: 0.5s;
}

@keyframes fadeIn {
	to {
		scale: 1;
		opacity: 1;
	}
}

/* Responsive */
/* Example: Mobile-first approach */

/* Media query for small devices */
/* Styles for small devices (e.g., smartphones) */
/* @media (min-width: 576px) {} */

/* Media query for medium devices */
